












import { Component, Prop, Vue } from "vue-property-decorator";
import { Dialog } from "vant";
@Component({
  components: {
    vanDialog: Dialog.Component,
  },
})
export default class extends Vue {
  @Prop({ type: Boolean, default: false }) confirm_btn?: boolean;
  @Prop({ type: Boolean, default: false }) cancel_btn?: boolean;
  @Prop(String) title: string | undefined;
  @Prop({ type: Function, default: () => {} }) cancel?: () => void;
  show: boolean = false;

  showDiaLog() {
    this.show = true;
  }
}
