import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);
const allOutTag = [
  "服饰",
  "餐饮",
  "购物",
  "生活服务",
  "住房",
  "交通",
  "旅游",
  "红包",
  "医疗",
];
const allInTag = ["工资", "退款", "转账", "理财", "其他"];
export default new Vuex.Store({
  state: {
    tagsOut: JSON.parse(
      localStorage.getItem("tagsOut") ||
        '["服饰", "餐饮", "购物","生活服务", "住房", "交通","旅游","红包","医疗"]'
    ),
    tagsOutDel: JSON.parse(localStorage.getItem("tagsOutDel") || "[]"),
    tagsIn: JSON.parse(
      localStorage.getItem("tagsIn") || '["工资","退款","转账","理财","其他"]'
    ),
    tagsInDel: JSON.parse(localStorage.getItem("tatagsInDel") || "[]"),
    recordList: JSON.parse(localStorage.getItem("recordList") || "[]"),
  },
  mutations: {
    tagsPush(state, value) {
      if (value[1] === "支出") {
        state.tagsOut.push(value[0]);
        localStorage.setItem("tagsOut", JSON.stringify(state.tagsOut));
      } else {
        state.tagsIn.push(value[0]);
        localStorage.setItem("tagsIn", JSON.stringify(state.tagsIn));
      }
    },
    tagsAdd(state, value) {
      if (value[1] === "支出") {
        state.tagsOutDel.forEach(
          (item: String, index: number, arr: String[]) => {
            if (item === value[0]) {
              arr.splice(index, 1);
            }
          }
        );
        state.tagsOut.push(value[0]);
        localStorage.setItem("tagsOut", JSON.stringify(state.tagsOut));
        localStorage.setItem("tagsOutDel", JSON.stringify(state.tagsOutDel));
      } else {
        state.tagsInDel.forEach(
          (item: String, index: number, arr: String[]) => {
            if (item === value[0]) {
              arr.splice(index, 1);
            }
          }
        );
        state.tagsIn.push(value[0]);
        localStorage.setItem("tagsIn", JSON.stringify(state.tagsIn));
        localStorage.setItem("tagsInDel", JSON.stringify(state.tagsInDel));
      }
    },
    tagsDel(state, value) {
      if (value[1] === "支出") {
        state.tagsOut.forEach((item: String, index: number, arr: String[]) => {
          if (item === value[0]) {
            const itemdel = arr.splice(index, 1);
            if (allOutTag.indexOf(itemdel[0] as string) >= 0) {
              state.tagsOutDel.unshift(itemdel[0]);
            }
          }
        });
        localStorage.setItem("tagsOut", JSON.stringify(state.tagsOut));
        localStorage.setItem("tagsOutDel", JSON.stringify(state.tagsOutDel));
      } else {
        state.tagsIn.forEach((item: String, index: number, arr: String[]) => {
          if (item === value[0]) {
            const itemdel = arr.splice(index, 1);
            if (allInTag.indexOf(itemdel[0] as string) >= 0) {
              state.tagsInDel.unshift(itemdel[0]);
            }
          }
        });
        localStorage.setItem("tagsIn", JSON.stringify(state.tagsIn));
        localStorage.setItem("tagsInDel", JSON.stringify(state.tagsInDel));
      }
    },
    recordListPush(state, value) {
      // console.log(value);

      state.recordList.push(value);
      localStorage.setItem("recordList", JSON.stringify(state.recordList));
    },
  },
  actions: {},
  modules: {},
});
